export default {
  props: ["tab", "loadingImport", "pushLoading", 'exportLoading', 'delLoading', 'items', 'AllChecked', 'tags', "allCnt", "doneCnt", "filtersParams", "filterAccountProps", "shopOptArr"],
  data: function data() {
    return {
      importUrl: ''
    };
  },
  methods: {
    gotoImportList: function gotoImportList(itms) {
      this.$emit('gotoImportList', itms);
    },
    delItem2: function delItem2(itms) {
      this.$emit('delItem2', itms);
    },
    clearFilter: function clearFilter() {
      this.$emit('clearFilter');
    },
    setFilter: function setFilter() {
      this.$emit('setFilter');
    },
    openPushSetting: function openPushSetting() {
      this.$emit('openPushSetting');
    },
    delItem: function delItem(itms) {
      this.$emit('delItem', itms);
    },
    importPorduct: function importPorduct() {
      this.$emit('importPorduct', this.importUrl);
      this.importUrl = '';
    },
    changeAllItems: function changeAllItems($event) {
      this.$emit('changeAllItems', $event);
    },
    batItems: function batItems(type) {
      this.$emit('batItems', type);
      if (type == 'addTags') this.$refs["tagMgt"].hide();
    },
    openTagMgt: function openTagMgt() {
      this.$emit('openTagMgt');
    },
    removeCheckedTags: function removeCheckedTags() {
      this.$emit('removeCheckedTags');
    },
    showPrice: function showPrice(item) {
      //显示几个范围
      var price = '---';
      var min = 0,
          max = 0;
      item.stocks.some(function (e) {
        if (e.costPrice) {
          min = max = Number(e.costPrice);
          return true;
        }
      });
      item.stocks.forEach(function (e) {
        if (e.costPrice && min > Number(e.costPrice)) {
          min = Number(e.costPrice);
        }

        if (e.costPrice && max < Number(e.costPrice)) {
          max = Number(e.costPrice);
        }
      });

      if (min == max) {
        price = min.toFixed(2);
      } else {
        price = "".concat(min, " - ").concat(max);
      }

      return "".concat(item.stocks.length ? item.stocks[0].currency : '', " ").concat(price);
    },
    singPush: function singPush(item) {
      this.$emit('singPush', item);
    },
    editProduct: function editProduct(item) {
      this.$emit('editProduct', item);
    }
  }
};